@media (max-width: 960px) {
  .header-menu-container.container {
    padding-left: 0;
    padding-right: 0;
    position: fixed;
    left: 0;
    top: 0;
    width: 300px;
    height: 100vh;
    background: #ffffff;
    z-index: 3;
    transition: transform 0.3s ease-in-out;
    transform: translateX(-100%);
    display: none;
  }
  .header-menu-container.container.active {
    left: 0;
    display: block;
    transition: transform 0.3s ease-in-out;
    transform: translateX(0%);
    border-right: 1px solid rgba(89, 89, 89, 0.2);
  }
}
@media (max-width: 450px) {
  .header-menu-container.container {
    width: 100vw;
    height: max-content;
    border-bottom: 1px solid rgba(89, 89, 89, 0.2);
    transform: translateY(-100%);
  }
  .header-menu-container.container.active {
    transform: translateY(0%);
  }
}
.header-menu-container.container .burger-icon-block {
  display: none;
}
@media (max-width: 960px) {
  .header-menu-container.container .burger-icon-block {
    padding: 13px 10px 14px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header-menu-container.container .burger-icon-block .logo {
    padding: 0;
    gap: 9px;
    flex-direction: row;
  }
  .header-menu-container.container .burger-icon-block .logo .logo-img {
    max-width: 34px;
    max-height: 33px;
  }
  .header-menu-container.container .burger-icon-block .logo .logo-text {
    text-align: start;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 960px) and (min-width: 961px) {
  .header-menu-container.container .burger-icon-block .burger-exit {
    display: none !important;
    width: fit-content;
    height: fit-content;
  }
}
@media (max-width: 960px) and (max-width: 960px) {
  .header-menu-container.container .burger-icon-block .burger-exit {
    display: block;
    width: 16px;
    height: 16px;
