.about-use-product-block {
  &__container {
    padding-top: 82px;
    padding-bottom: 100px;
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    .title-block-about-use-product {
      .title-block-about-use-product__title {
        @include rale-800-45;
        text-align: center;
      }
      .title-block-about-use-product__description {
        @include rale-500-18;
        max-width: 590px;
        text-align: center;
        margin-top: 30px;
        line-height: 28px;
        margin-left: auto;
        margin-right: auto;
        @media (max-width: $mobile) {
          line-height: 20px;
          margin-top: 0px;
        }
      }
    }
    .list-about-use-product {
      margin-top: 60px;
      display: flex;
      gap: 32px;
      @media (max-width: 960px) {
        flex-direction: column;
        align-items: center;
      }
      @media (max-width: $mobile) {
        margin-top: 20px;
        gap: 10px;
      }
    }

    .card {
      max-width: 410px;
      height: 525px;
      width: 100%;
      position: relative;
      perspective: 1000px;
    }
    .front,
    .back {
      padding: 10px 10px;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 1s;
      backface-visibility: hidden;
    }

    .front {
      flex-direction: column;
    }

    .front img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
    .back {
      transform: rotateY(180deg);
      flex-direction: column;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($color: $darkGreen, $alpha: 0.9);
        z-index: 0;
      }
      img {
        background-color: $darkGreen;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
    .card:hover .front {
      transform: rotateY(180deg);
    }
    .card:hover .back {
      transform: rotateY(360deg);
    }

    .item-about-use-product {
      width: 410px;
      height: 525px;
      display: flex;
      position: relative;
      flex-direction: column;
      align-items: center;
      &__icon {
        z-index: 2;
        margin-left: 10px;
        color: $white;
        &.icon-walnut {
          font-size: 76px;
        }
        &.icon-hazelnut {
          font-size: 51px;
        }
        &.icon-rosehip {
          font-size: 58px;
        }
      }
      &__title {
        text-align: center;
        z-index: 2;
        @include rale-800-40;
        color: $white;
        margin-top: 10px;
      }
      &__description {
        p {
          margin-top: 20px;
        }
        z-index: 2;
        @include rale-600-18;
        color: $white;
        text-align: center;
        max-width: 330px;
      }
    }
  }
}
