@import "../../products.scss";

.main-page-products {
  &__title-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 28px;
    @media (max-width: $mobile) {
      gap: 0px;
    }
  }
  &__title {
    @include rale-800-45;
  }
  &__description {
    @include rale-500-18;
    max-width: 620px;
    text-align: center;
    letter-spacing: 0.02em;
    line-height: 28px;
    @media (max-width: $mobile) {
      line-height: 20px;
    }
  }
  .rectangle.unbackground.products__button {
    margin-top: 72px;
    padding: 17px 31px 16px 31px;
    border-width: 2px;
  }
  .products__products-list.products-list{
    margin-top: 61px;
  }
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 85px;
    padding-bottom: 90px;
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
      gap: 20px;
    }
  }
}
