@import "./default.scss";
.body-banner {
  background: rgba(0, 0, 0, 0.45);
  position: relative;
  height: 600px;
  &.video {
    cursor: pointer;
    &:hover {
      background: rgba(0, 0, 0, 0.55);
    }
  }
  @media (max-width: $mobile) {
    max-height: 450px;
  }
  overflow: hidden;
  img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  .banner-container {
    margin-top: 195px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: 100%;
    gap: 28px;
    @media (max-width: $tablet) {
      margin-top: auto;
      justify-content: center;
    }
    @media (max-width: $mobile) {
      gap: 11px;
    }
    &__title {
      @include rale-800-62;
      color: $white;
      @media (max-width: $mobile) {
        margin-top: 17px;
      }
    }
    &__description {
      max-width: 550px;
      @include rale-600-25;
      line-height: 32px;
      margin-top: 2px;
      margin-right: 1px;
      color: $white;
      text-align: center;
      @media (max-width: $mobile) {
        margin-top: 0;
        max-width: 300px;
        line-height: 23px;
        letter-spacing: 0.01em;
      }
    }
    .icon-play {
      margin-top: 10px;
      color: $white;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 61px;
      height: 61px;
      border: 2px solid $white;
      border-radius: 50%;
      font-size: 17px;
      &::before {
        margin-left: 5px;
      }
      @media (max-width: $mobile) {
        width: 45px;
        height: 45px;
        &::before {
          font-size: 13px;
        }
      }
    }
  }
  img {
  }
}
